*{
  font-family: 'Circular Std Book Bold','Poppins', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: 0;
}

/* ********************************* Variables ******************************************** */
:root{
  --color-gray: rgb(48,53,55);
  --color-lighter-gray: rgb(91,104,105);
  --color-gold: rgb(173,148,84);
  --color-green: rgb(64,88,96);
  --color-white: rgb(239,238,228);

  transition-duration: 0.6s;
}

body{
  background-color: var(--color-gray);
}
/* ********************************* Header ******************************************** */
.header-container{
  height: 90vh;
  width: 100%;
  background-color: var(--color-gray);
  color: var(--color-gold);
}

.sam-h{
  position: relative;
  width: 100%;
  text-align: center;
  top: 20%;
  font-size: 500%;
}

.sam-h-tech-stack{
  position: relative;
  width: 100%;
  text-align: center;
  top: 50%;
  font-size: 100%;
  color: var(--color-white)
}

@media only screen and (min-width: 1200px){
  .sam-h{
    font-size: 700%;
  }
}

/* ********************************* Footer ******************************************** */
.footer-container{
  height: 70vh;
  background-color: black;
  color: var(--color-white);
  padding: 50px 20px 0 20%;
}

.footer-title{
  margin-bottom: 20px;
  font-size: 150%;
  color: var(--color-gold);
}

.footer-contact-container{
  margin-top: 50px;
  text-align: center;
}

.footer-contact{
  padding: 10px;
  transition-duration: 0.5s;
}

.footer-link{
  text-decoration: none;
}

.footer-link:hover{
  color: var(--color-gold);
}

.footer-contact:hover{
  transform: scale(1.6);
  transition-duration: 0.5s;
  color: var(--color-gold);
}

.footer-contact > a{
  color: var(--color-white);
}

.footer-contact > a:visited{
  color: var(--color-white);
}

.footer-contact > a:visited:hover{
  color: var(--color-white);
  color: var(--color-gold);
}

@media only screen and (min-width: 1200px){
  .footer-container{
    height: 55vh
  }

  .footer-title{
    font-size: 300%;
    margin-bottom: 8%;
  }

  .footer-contact-container{
    display: flex;
    justify-content: space-evenly;
    margin-top: 6%;
  }
}
    

/* ********************************* Animation effects for about component ******************************************** */

@keyframes inAnimation{
  0%{
    opacity: 1;
    visibility: hidden;
  }
  100%{
    opacity: 0;
    visibility: visible;
  }
}

@keyframes outAnimation{
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
    visibility: hidden;
  }
}